.intro {
  position: relative;
  margin-bottom: 2.4rem;
  height: 100vh;
  display: grid;
  grid-template-columns: 56% 34%;
  align-items: center;

  border-bottom-right-radius: 1.5rem;
  border-bottom-left-radius: 1.5rem;
  overflow: hidden;
}

.intro__photo_wrapper {
  position: absolute;
  height: 100%;
  width: 100%;
}

.intro__banner {
  width: 100%;
  height: 100%;
  object-fit: cover;

}

.intro__content {
  position: relative;
  z-index: 2;
  grid-column: 2;
  display: grid;
  grid-gap: 3.2rem;

  color: var(--font-color-white);
  font-size: 1.8rem;
  line-height: 2.5rem;
}

.intro__form {
  width: fit-content;
  display: flex;
  align-items: center;
  padding: 0.9rem 1.8rem;
  background-color: var(--font-color-white);
  border-radius: 4rem;
}

.intro__form_input {
  width: 16rem;
}

.intro__text_content {
  display: grid;
  grid-gap: 2.2rem;
}

.intro__title {
  font-size: 4.8rem;
  line-height: 5.7rem;
  font-family: 'Inter', sans-serif;
  text-transform: uppercase;
}

.intro__blur {
  position: absolute;
  z-index: 1;
  content: '';
  right: 0;
  top: 0;
  height: 100%;
  width: 45%;
  backdrop-filter: blur(10);
  display: flex;

}

.intro__blur_item {
  height: 100%;
  width: 100%;
  background-color: #71717161;
  box-shadow: -3px 4px 13px 0 #00000080 inset;
  filter: drop-shadow(0px 4px 4px #00000040);
  transition: background-color .1s;

  &:hover {
    background-color: #26262661;
  }
}

.intro__calculate_btn {
  position: absolute;
  z-index: 2;
  bottom: 4rem;
  left: 3.2rem;
}

