* {
  box-sizing: border-box;
}

html, body {
  font-family: "Roboto", sans-serif;
  color: var(--font-color-primary);
  font-size: 10px;
}


html,
body,
h1,
h2,
h3,
h4,
p,
ul {
  padding: 0;
  margin: 0;
}

ul {
  list-style: none;
  color: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
}

input {
  outline: none;
  border: none;
  font-size: inherit;
}